import React, { Component } from "react";
import Container from "react-bootstrap/Container";
import Complated from "./Complated";
import ListGroup from "react-bootstrap/ListGroup";

export default class AddAnime extends Component {
  constructor() {
    super();
    this.state = {};
  }

  divClicked = (e, index) => {
    e.preventDefault();
    this.youDidIt();
    console.log(e);
    console.log(index);
    this.props.allTimeAnime(index);
  };

  youDidIt = (e) => {
    console.log("Yes I'm here");
  };

  removeMe = (index) => {
    this.props.delete([index]);
  };

  checkBoxHandler = (index) => {
    this.props.checkbox(index);
  };

  render() {
    return (
      <div>
        {this.props.sAnime.map((e, index) => (
          <div style={{ display: "flex" }}>
            <div
              className="animeList"
              onClick={(e) => this.divClicked(e, index)}
            >
              <Container className="flex-container">
                <ListGroup horizontal>
                  <ListGroup.Item style={{ width: "5%" }}>
                    {index + 1}
                  </ListGroup.Item>
                  <ListGroup.Item style={{ width: "25%" }}>
                    <img
                      width={70}
                      height={100}
                      className="mr-3"
                      src={e.image_url}
                      alt="Generic placeholder"
                    />
                  </ListGroup.Item>

                  <ListGroup.Item>{e.title}</ListGroup.Item>

                  <ListGroup.Item>{e.episodes + " ep "}</ListGroup.Item>
                  <ListGroup.Item variant="dark">{e.score}</ListGroup.Item>
                </ListGroup>
              </Container>
            </div>
            <ListGroup.Item style={{ padding: "1%" }}>
              <div onClick={(e) => this.removeMe(index)}>
                <svg
                  id="xButton"
                  xmlns="http://www.w3.org/2000/svg"
                  width="16"
                  height="16"
                  fill="currentColor"
                  class="bi bi-x-circle-fill"
                  viewBox="0 0 16 16"
                >
                  <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM5.354 4.646a.5.5 0 1 0-.708.708L7.293 8l-2.647 2.646a.5.5 0 0 0 .708.708L8 8.707l2.646 2.647a.5.5 0 0 0 .708-.708L8.707 8l2.647-2.646a.5.5 0 0 0-.708-.708L8 7.293 5.354 4.646z" />
                </svg>
              </div>
              <input
                type="checkbox"
                onChange={() => this.checkBoxHandler(index)}
              />
              <Complated />
            </ListGroup.Item>
          </div>
        ))}
      </div>
    );
  }
}
