import React, { Component } from "react";
import Card from "react-bootstrap/Card";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import FormControl from "react-bootstrap/FormControl";
import AddAnime from "./AddAnime";
import axios from "axios";
import Alltime from "./Alltime";

export default class Tier extends Component {
  constructor() {
    super();
    this.state = {
      animeList: [],
      animeNames: [],
      currentAnime: "",
      bestAnime: false,
      animeSelectedDeleted: [],
    };
  }

  handleChange = (e) => {
    this.setState({ currentAnime: e.target.value });
  };

  handleSubmet = (e) => {
    console.log(e);
    this.animeSearch();
    this.setState({ currentAnime: "" });
  };

  removeAllList = (e) => {
    this.setState({
      ...this.state,
      animeList: [],
      animeNames: [],
      bestAnime: false,
    });
  };

  deleteHandler = (indexes) => {
    console.log(indexes);
    let tmpArr = this.state.animeList.filter((v, i) => {
      return indexes.indexOf(i) === -1;
    });
    let tmpArr2 = this.state.animeNames.filter((v, i) => {
      return indexes.indexOf(i) === -1;
    });
    this.setState({
      ...this.state,
      animeList: [...tmpArr],
      animeNames: [...tmpArr2],
      animeSelectedDeleted: [],
      bestAnime: false,
    });
  };

  handleCheckBox = (index) => {
    let tmpArr = [...this.state.animeSelectedDeleted, index];
    this.setState({
      ...this.state,
      animeSelectedDeleted: [...tmpArr],
    });
  };

  animeSearch = () => {
    console.log("animeSearch");
    axios
      .get(`https://api.jikan.moe/v3/search/anime?q=${this.state.currentAnime}`)

      .then((respone) => {
        console.log("resoponse", respone.data.results[0]);
        let found = this.state.animeNames;
        let check = found.includes(respone.data.results[0].title);
        if (check === false) {
          this.setState({
            ...this.state,
            animeList: [...this.state.animeList, respone.data.results[0]],
          });
          let animeNames = this.state.animeNames;
          animeNames.push(respone.data.results[0].title);
          this.setState({ animeNames });
        }
      });
  };

  allTimeAnime = (e) => {
    this.setState({ bestAnime: e });
  };

  render() {
    return (
      <Container>
        <Col>
          <div id="sTier">
            <p>{this.props.tier} Tier</p>
          </div>
          <div id="sAllTime">
            {this.state.bestAnime === false ? null : (
              <Alltime
                bestAnime={this.state.bestAnime}
                animeList={this.state.animeList}
              />
            )}
          </div>

          <Row>
            <div className="searchDev">
              <Card>
                <Card.Body>
                  <Form inline>
                    <FormControl
                      type="text"
                      placeholder="Search"
                      className=" mr-sm-2"
                      id="search"
                      onChange={(e) => this.handleChange(e)}
                      value={this.state.currentAnime}
                    />
                    <Button onClick={(e) => this.handleSubmet(e)}>Add</Button>
                  </Form>
                </Card.Body>
              </Card>
              <AddAnime
                sAnime={this.state.animeList}
                allTimeAnime={this.allTimeAnime}
                index={this.state.index}
                delete={this.deleteHandler}
                checkbox={this.handleCheckBox}
              />
              <Button onClick={(e) => this.removeAllList(e)}>Delete All</Button>
              <Button
                onClick={(e) =>
                  this.deleteHandler(this.state.animeSelectedDeleted)
                }
              >
                Delete selected Item
              </Button>
              <hr />
            </div>
          </Row>
        </Col>
      </Container>
    );
  }
}
