import React, { Component } from "react";
import Card from "react-bootstrap/Card";

export default class Alltime extends Component {
  render() {
    // console.log(this.props.bestAnime);
    console.log(this.props.animeList[this.props.bestAnime]);
    const bestAnime = this.props.animeList[this.props.bestAnime];
    return (
      <div>
        <Card>
          <Card.Img variant="top" src={bestAnime.image_url} className="image" />
          <Card.Body>
            <Card.Title>{bestAnime.title}</Card.Title>
            <Card.Text>{bestAnime.synopsis}</Card.Text>
          </Card.Body>
          <Card.Footer>
            <small className="text-muted"> {bestAnime.score}</small>
          </Card.Footer>
        </Card>
      </div>
    );
  }
}
